.card-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 400px;
}

.widget {
  background: linear-gradient(to bottom right, #3cc0fe 20%, #06f);
  width: 900px;
  height: 400px;
  border-radius: 6px;
  position: relative;
  /* overflow: hidden; */
  display: flex;
  flex-wrap: wrap;
}

.details {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  margin-left: 60px;
}
.temperature {
  color: white;
  font-weight: 300;
  font-size: 5em;
}
.summary {
  color: #d2e9fa;
  font-size: 2em;
  font-weight: 300;
  width: 260px;
  margin-top: -16px;
  padding-bottom: 16px;
  border-bottom: 2px solid #9cd0ff;
  margin-left: 8px;
}
.summaryText {
  margin: 0;
  margin-left: 56px;
}
.precipitation,
.wind {
  color: #d2e9fa;
  font-size: 1.6em;
  font-weight: 300;
  margin-left: 8px;
}
.precipitation {
  margin-top: 16px;
}
.wind {
  margin-top: 4px;
}

@media (max-width: 750px) {
  .card-container {
    width: 90%;
  }
  .temperature {
    color: white;
    font-weight: 300;
    font-size: 3em;
  }
  .details {
    margin-left: 20px;
  }
}

.weather-wrapper {
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.weather-card {
  margin: 20px 5px;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  width: 270px;
  height: 270px;
  background-color: white;
  box-shadow: 0px 0px 25px 1px rgba(50, 50, 50, 0.1);
  animation: appear 500ms ease-out forwards;
  background-color: rgb(49, 138, 255);
}
.weather-card h1 {
  position: absolute;
  font-weight: 300;
  font-size: 80px;
  color: #ffffff;
  bottom: 0;
  left: 35px;
  opacity: 0;
  transform: translateX(150px);
  animation: title-appear 500ms ease-out 500ms forwards;
}
.weather-card p {
  position: absolute;
  font-weight: 300;
  font-size: 24px;
  color: #ffffff;
  bottom: 0;
  left: 25px;
  animation: title-appear 1s ease-out 500ms forwards;
}
.weather-icon {
  position: relative;
  width: 50px;
  height: 50px;
  top: 0;
  float: right;
  margin: 40px 40px 0 0;
  animation: weather-icon-move 5s ease-in-out infinite;
}
.sun {
  background: #ffcd41;
  border-radius: 50%;
  box-shadow: rgba(255, 255, 0, 0.1) 0 0 0 4px;
  animation: light 800ms ease-in-out infinite alternate,
    weather-icon-move 5s ease-in-out infinite;
}
@keyframes light {
  from {
    box-shadow: rgba(255, 255, 0, 0.2) 0 0 0 10px;
  }
  to {
    box-shadow: rgba(255, 255, 0, 0.2) 0 0 0 17px;
  }
}
.cloud {
  margin-right: 60px;
  background: #b6cede;
  border-radius: 20px;
  width: 25px;
  height: 25px;
  box-shadow: #b6cede 24px -6px 0 2px, #b6cede 10px 5px 0 5px,
    #b6cede 30px 5px 0 2px, #b6cede 11px -8px 0 -3px, #b6cede 25px 11px 0 -1px;
}
.cloud:after {
  content: '';
  position: absolute;
  border-radius: 10px;
  background-color: transparent;
  width: 4px;
  height: 12px;
  left: 0;
  top: 31px;
  transform: rotate(30deg);
  animation: rain 800ms ease-in-out infinite alternate;
}
@keyframes rain {
  from {
    box-shadow: #ffffff 8px 0px, #ffffff 32px -6px, #ffffff 20px 0px;
  }
  to {
    box-shadow: #ffffff 8px 6px, #ffffff 32px 0px, #ffffff 20px 6px;
  }
}
@keyframes weather-icon-move {
  50% {
    transform: translateY(-8px);
  }
}
.inspiration {
  margin-top: 80px;
  color: #709fbe;
  font-weight: 300;
  font-size: 24px;
  text-align: center;
}
.inspiration a {
  color: #ff8f8f;
  font-weight: 400;
  animation: all 300ms ease-in-out;
}
@keyframes appear {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.05);
  }
  75% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes title-appear {
  from {
    opacity: 0;
    transform: translateX(150px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

.weather-of-the-week {
  display: flex;
  flex-wrap: wrap;
}

@media (max-width: 500px) {
  .weather-of-the-week {
    justify-content: center;
  }
}

